<template>
  <div class="tab-wrapper">
    <span>{{activeNum}} Items</span>
    <ul class="nav-list">
      <li 
        v-for="(state,index) in states" 
        :key="index" 
        :class="{clicked: filter === state}"
        @click="toggleState($event,state)"
      >{{state}}</li>
      <span class="line"></span>
    </ul>
    <div class="clear"
      @click="clear"
    >Clear</div>
  </div>
</template>

<script>
  export default {
    props: {
      filter: String,
      todos: Array
    },
    data() {
      return {
        states: ['All', 'Active', 'Completed']
      }
    },
    computed: {
      activeNum() {
        return this.todos.filter(todo => !todo.isCompleted).length;
      }
    },
    methods: {
      toggleState(e,state) {
        this.$emit('toggleState',state);
        const eDom = e.target;
        $('.line').width($(eDom).innerWidth()).css("left", $(eDom).position().left);
        
     },
      clear() {
        this.$emit('clear');
      }
    }
  }
</script>

<style lang="scss" scoped>
  .tab-wrapper {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    padding: 1em .8em;
    margin: .5em 0 1em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, .15);
    box-shadow: 0px 1px 20px rgba($color: #cdcdcd, $alpha: .4);
    &:before{
      content: '';
      width: 100%;
      height: .5em;
      position: absolute;
      left: 0;
      bottom: -.5em;
      border-bottom: 1px solid rgba(0, 0, 0, .1);
      box-shadow: 0px 2px 20px rgba($color: #cdcdcd, $alpha: .3);
    }
    &::after{
      content: '';
      width: 100%;
      height: 1em;
      position: absolute;
      left: 0;
      bottom: -1em;
      border-bottom: 1px solid rgba(0, 0, 0, .1);
      box-shadow: 0px 2px 10px rgba($color: #cdcdcd, $alpha: .6);
    }

    span {
      float: left;
      font-size: .8em;
    }
    ul {
      display: inline-block;
      position: relative;
      list-style: none;
      li {
        cursor: pointer;
        user-select: none;
        font-size: .8em;
        display: inline-block;
        padding: .5em .7em;
        position: relative;
        &:hover{
        background-color: #f2f2f2;
        }
      }
      .line{
        height: 2px;
        width: 32px;
        background-color: #4199ee;
        position: absolute;
        transition: all .2s ease;
      }
    }
    .clear {
      cursor: pointer;
      user-select: none;
      float: right;
      font-size: .8em;
    }
  }
</style>



