<template>
  <div class="todo-wrapper">
    <!-- 输入框 -->
    <input type="text" :placeholder="title" 
      @keydown.enter="addToDo" 
    />
    <!-- tab选项 -->
    <tab 
      :filter="filter" 
      :todos="todos" 
      @toggleState="toggleToDos" 
      @clear="clearCompleted"
    />
    <!-- todo列表 -->
    <ul>
      <li 
        is="item" 
        v-for="todo in filteredTodos" 
        :key="todo.id" 
        :todo="todo" 
        @toDel="delToDo" 
        @toUpdate="updateToDo"
      ></li>
    </ul>
  </div>
</template>

<script>
  import store from '../base/store'
  import item from './item'
  import tab from './tab'
  let todos = store.get(),
      id = todos[0] ? todos[0].id + 1 : 0;
  export default {
    components: {
      item,
      tab
    },
    data() {
      return {
        title: '接下来要做什么？',
        todos: todos,
        filter: 'All'
      }
    },
    computed: {
      filteredTodos() {
        if (this.filter === 'All') {
          return this.todos;
        }
        const completed = this.filter === 'Completed';
        return this.todos.filter(todo => todo.isCompleted === completed);
      }
    },
    methods: {
      addToDo(e) {
        if (e.target.value === '') return;
        this.todos.unshift({
          id: id++,
          content: e.target.value.trim(),
          isCompleted: false
        });
        e.target.value = '';
        this.$notify({
          content: '你又多一件事待做！'
        })
      },
      delToDo(key) {
        this.todos.splice(this.todos.findIndex(todo => todo.id === key), 1);
        this.$notify({
          content: '你又少一件事待做了...'
        })
      },
      updateToDo(item) {
        this.todos.map(todo => todo.id === item.id ? item : todo);
        this.$notify({
          content: '厉害厉害，做完了一件事！'
        })
      },
      toggleToDos(state) {
        this.filter = state;
      },
      clearCompleted() {
        this.todos = this.todos.filter(todo => !todo.isCompleted);
        this.$notify({
          content: '厉害炸了，这么多完成的事被清除！'
        })
      }
    },
    watch: {
      todos: {
        handler: function() {
          store.save(this.todos);
        },
        deep: true
      }
    }
  }
</script>

<style lang="scss" scoped>
  .todo-wrapper {
    width: 90%;
    margin: 2em auto 0 auto;
    input {
      font-size: 1em;
      height: 3em;
      width: 100%;
      outline: none;
      border: 0;
      padding: 0 1em;
      margin-bottom: 1em;
      box-sizing: border-box;
      border-bottom: .8px solid rgba($color:#000, $alpha: .4);
      &:focus{
      border-bottom: 2px solid rgba($color:#4199ee, $alpha: 1);
      }
    }
    ul {
      list-style: none;
      max-height: 18em;
      overflow: auto;
      &::-webkit-scrollbar{
        width: 3px;
      }
      &::-webkit-scrollbar-thumb{
        background-color: rgba($color:#4199ee, $alpha: .5);
      }
      li {
        margin: .7em auto;
        width: 95%;
        border-radius: 7px;
        box-shadow: 1px 0 10px rgba($color: #cdcdcd, $alpha: .6);
      }
    }
  }
</style>



